import { Item, Segment, Container } from "semantic-ui-react";
const Playlist = ({ playlist }) => (
  <Item className="ui inverted">
    <Item.Image size="small" src={playlist.snippet.thumbnails.medium.url} />
    <Item.Content>
      <Item.Header as="a">{playlist.snippet.title}</Item.Header>

      <Item.Description>
        <p>
          Many people also have their own barometers for what makes a cute dog.
        </p>
      </Item.Description>
    </Item.Content>
  </Item>
);

export default React.memo(Playlist);
